<template>
    <div>
        <div @click="alert"></div>

        <div @click="alert2" class="alert2"></div>
    </div>
</template>
<style lang="scss" scoped>
    div{
        font-size: .32rem;
    }
    .alert2{
        position: fixed;
        right: .3rem;
        bottom: .3rem;
    }
</style>
<script type="text/ecmascript-6">
    import {Alert,Toask} from '../../../build/';
    export default{
        data(){
            return{
                clickAlert1:false,
            }
        },
        methods:{
            alert(){
                Alert('');
                this.clickAlert1 = true;
            },
            alert2(){
                if(this.clickAlert1){
                    Alert({
                        title:"",
                        content:'<div></div>'
                    }).then((data)=>{
                    })
                    return
                }
                Toask('')
            }
        },
    }
</script>



